import React from 'react'
import { withStyles } from '@material-ui/styles'

//import ImportExportIcon from '@material-ui/icons/ImportExport'
import MdiIcon from '@mdi/react'
import { mdiEthernetCable, mdiAccessPoint } from '@mdi/js'

class ThingConnection extends React.PureComponent {
  isEthernet() {
    const { thing, thingShadow } = this.props
    if (!thingShadow) {
      return thing.conn === 'eth'
    } else {
      return thingShadow.state.reported.conn === 'eth'
    }
  }

  isGsm() {
    const { thing, thingShadow } = this.props
    if (!thingShadow) {
      return thing.conn === 'gsm'
    } else {
      return thingShadow.state.reported.conn === 'gsm'
    }
  }

  render() {
    const {
      classes,
      thing,
      thingShadow,
      hideIcon = false,
      hideLabel = false,
      hideValue = false,
    } = this.props
    if (!thing && !thingShadow) {
      return null
    }
    return (
      <span className={classes.root}>
        {!hideIcon &&
          (this.isEthernet() ? (
            <MdiIcon
              className={`${classes.icon} ${classes.icon_textSecondary}`}
              path={mdiEthernetCable}
              size={1}
              style={{ fill: 'currentColor' }}
            />
          ) : (
            <MdiIcon
              className={`${classes.icon} ${classes.icon_textSecondary}`}
              path={mdiAccessPoint}
              size={1}
              style={{ fill: 'currentColor' }}
            />
          ))}
        {!hideLabel && <span className={classes.label}> </span>}
        {!hideValue &&
          (this.isEthernet() ? (
            <span className={classes.value}>Ethernet</span>
          ) : this.isGsm() ? (
            <span className={classes.value}>GSM</span>
          ) : (
            <span className={classes.value}></span>
          ))}
      </span>
    )
  }
}

const styles = theme => ({
  root: {},
  icon: {
    position: 'relative',
    top: -1,
    marginRight: 8,
    verticalAlign: 'middle',
  },
  icon_textPrimary: {
    color: theme.palette.text.primary,
  },
  icon_textSecondary: {
    color: theme.palette.text.secondary,
  },
})

export default withStyles(styles)(ThingConnection)
