import React from 'react'
import { withStyles } from '@material-ui/styles'

import LockIcon from '@material-ui/icons/LockOutlined'
import MdiIcon from '@mdi/react'
import { mdiLockOpen } from '@mdi/js'

class ThingGuard extends React.PureComponent {
  isActive() {
    const { thing, thingShadow } = this.props
    if (!thingShadow) {
      return thing.guard === true || thing.guard === 'true'
    } else {
      return thingShadow.state.reported.guard === true
    }
  }

  render() {
    const {
      classes,
      thing,
      thingShadow,
      hideIcon = false,
      hideLabel = false,
      hideValue = false,
      whenActive,
      whenInactive,
    } = this.props
    if (!thing && !thingShadow) {
      return null
    }
    return (
      <span className={classes.root}>
        {!hideIcon &&
          (this.isActive() ? (
            <LockIcon
              className={`${classes.icon} ${classes.icon_textSecondary}`}
            />
          ) : (
            <MdiIcon
              className={`${classes.icon} ${classes.icon_textPrimary}`}
              path={mdiLockOpen}
              size={1}
              style={{ fill: 'currentColor' }}
            />
          ))}
        {!hideLabel && <span className={classes.label}> </span>}
        {!hideValue &&
          (this.isActive() ? (
            <span className={classes.value}></span>
          ) : (
            <strong className={classes.value}></strong>
          ))}
        {whenActive && this.isActive() && whenActive}
        {whenInactive && !this.isActive() && whenInactive}
      </span>
    )
  }
}

const styles = theme => ({
  root: {},
  icon: {
    position: 'relative',
    top: -1,
    marginRight: 8,
    verticalAlign: 'middle',
  },
  icon_textPrimary: {
    color: theme.palette.text.primary,
  },
  icon_textSecondary: {
    color: theme.palette.text.secondary,
  },
})

export default withStyles(styles)(ThingGuard)
