import React from 'react'
import { withStyles } from '@material-ui/styles'

import NotificationsActiveIcon from '@material-ui/icons/NotificationsActive'
import NotificationsIcon from '@material-ui/icons/NotificationsOutlined'

class ThingAlarm extends React.PureComponent {
  isActive() {
    const { thing, thingShadow } = this.props
    if (!thingShadow) {
      return thing.alarm === true || thing.alarm === 'true'
    } else {
      return thingShadow.state.reported.alarm === true
    }
  }

  render() {
    const {
      classes,
      thing,
      thingShadow,
      hideIcon = false,
      hideLabel = false,
      hideValue = false,
      whenActive = null,
    } = this.props
    if (!thing && !thingShadow) {
      return null
    }
    return (
      <span className={classes.root}>
        {!hideIcon &&
          (this.isActive() ? (
            <NotificationsActiveIcon
              className={`${classes.icon} ${classes.icon_textPrimary}`}
            />
          ) : (
            <NotificationsIcon
              className={`${classes.icon} ${classes.icon_textSecondary}`}
            />
          ))}
        {!hideLabel && <span className={classes.label}></span>}
        {!hideValue &&
          (this.isActive() ? (
            <strong className={classes.value}></strong>
          ) : (
            <span className={classes.value}> </span>
          ))}
        {whenActive && this.isActive() && whenActive}
      </span>
    )
  }
}

const styles = theme => ({
  root: {},
  icon: {
    position: 'relative',
    top: -1,
    marginRight: 8,
    verticalAlign: 'middle',
  },
  icon_textPrimary: {
    color: theme.palette.text.primary,
  },
  icon_textSecondary: {
    color: theme.palette.text.secondary,
  },
})

export default withStyles(styles)(ThingAlarm)
