import React from 'react'
import { withStyles } from '@material-ui/styles'

import PowerIcon from '@material-ui/icons/PowerOutlined'
import PowerOffIcon from '@material-ui/icons/PowerOff'

class ThingPower extends React.PureComponent {
  isActive() {
    const { thing, thingShadow } = this.props
    if (!thingShadow) {
      return thing.pwr === true || thing.pwr === 'true'
    } else {
      return thingShadow.state.reported.pwr === true
    }
  }

  render() {
    const {
      classes,
      thing,
      thingShadow,
      hideIcon = false,
      hideLabel = false,
      hideValue = false,
    } = this.props
    if (!thing && !thingShadow) {
      return null
    }
    return (
      <span className={classes.root}>
        {!hideIcon &&
          (this.isActive() ? (
            <PowerIcon
              className={`${classes.icon} ${classes.icon_textSecondary}`}
            />
          ) : (
            <PowerOffIcon
              className={`${classes.icon} ${classes.icon_textPrimary}`}
            />
          ))}
        {!hideLabel && <span className={classes.label}></span>}
        {!hideValue &&
          (this.isActive() ? (
            <span className={classes.value}></span>
          ) : (
            <strong className={classes.value}> </strong>
          ))}
      </span>
    )
  }
}

const styles = theme => ({
  root: {},
  icon: {
    position: 'relative',
    top: -1,
    marginRight: 8,
    verticalAlign: 'middle',
  },
  icon_textPrimary: {
    color: theme.palette.text.primary,
  },
  icon_textSecondary: {
    color: theme.palette.text.secondary,
  },
})

export default withStyles(styles)(ThingPower)
